import Container from "../../common/Container";
import Header from "../../common/Header";
import Section from "../../common/Section";

const Author = () => (
    <Container>
        <Header title="O autorze" />
        <Section
            title="Przemysaw Biernat"
            body={
                <>
                    <p class="main__paragraph">Mam na imi Przemek. Pochodz z maej wsi, ktra znajduje sie niedaleko ywca. Jestem
                        fanem <b>piki noej</b> ale nie tylko,
                        poniewa inne sporty zespoowe jak i solowe nie s mi obce.
                    </p>
                </>}
        />
    </Container>
);
export default Author;